.home{
    display: flex;
    .homeContainer{
        flex: 6;
        
        .widgets, .chart{
            display: flex;
            padding: 20px;
            gap: 20px;
        }
        .chart{
            padding: 5px 20px;
        }
        .listContainer{
            -webkit-box-shadow: 0px 2px 7px 3px rgba(0,0,0,0.3); 
            box-shadow: 0px 2px 7px 3px rgba(0,0,0,0.3);
            padding: 20px;
            margin: 20px;

            .listTitle{
                font-weight: 500;
                color: gray;
                margin-bottom: 15px;
            }

        }
    } 
   
}

