.Featured{
 flex: 2;
 padding: 10px;
 -webkit-box-shadow: 0px 2px 7px 3px rgba(0, 0, 0, 0.138); 
 box-shadow: 0px 2px 7px 3px rgba(0, 0, 0, 0.131);

 .top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: gray;
    .moreVert{
        font-size: medium;
        cursor: pointer;
    }
 }
 .title{
    font-size: 16px;
    font-weight: 500;
 }
 .bottom{
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 15px;
    .featuredChart{
        width: 100px;
        height: 100px;
    }
    .title{
        font-weight: 500;
        color: gray;
    }
    .ammount{
        font-size: 30px;
    }
    .desc{
        font-weight: 300;
        font-size: 12px;
        color: gray;
        text-align: center;
    }

    .summary{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .item{
            text-align: center;
            .itemTitle{
                font-size: 14px;
                color: gray;
            }
            .itemResult{
                display: flex;
                align-items: center;
                margin-top: 10px;
                font-size: 14px;
                
                &.possitive{
                    color: green;
                }
                &.negative{
                    color: red;
                }
            }
            
        }
    }
 }
}