.Sidebar{
    flex: 1;
    border-right: 0.5px solid lightgray ;
    min-height: 100vh;
    background-color: white;

    .top{
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        
        
        .logo{
            font-size: 20px;
            font-weight: bold;
            color: #6439ff;
            margin-right: 40px;
        }
    }
    hr{
        height: 0px;
         border: 0.5px solid lightgray;
    
    }
  
    .center{
        padding-left: 10px;
        ui{
            list-style: none;
            margin: 0px;
            padding: 0px;

            .title{
                font-size: 10px;
                font-weight: bold;
                color: #999;
                margin-top: 15px;
                margin-bottom: 5px;
            }

            li{
                display: flex;
                align-items: center;
                padding: 5px;
                cursor: pointer;
                &:hover{
                    background-color: #ece8ff;
                    
                }
                .icon{
                    font-size: 18px;
                    color: #7451f8;
                
                }
                span{
                    font-size: 13px;
                    font-weight: 600;
                    color: #888;
                    padding: 5px;
                }

                
            }
        }   
    }

    .bottom{
        display: flex;
        align-items: center;
        margin: 10px;
       

        .colorOption{
            width: 20px;
            height: 20px;
            border-radius: 5px;
            border: 1px solid #7451f8 ;
            cursor: pointer;
            &:hover{
                width: 25px;
                height: 25px;
                border-radius: 5px;
                background-color: #ece8ff;
                
            }

            &:nth-child(1){
                background-color: white;
            }
            &:nth-child(2){
                background-color: #333;
            }
            &:nth-child(3){
                background-color: darkblue;
            }
            margin: 5px;
        }
    }
}