.Datatable{
 height: 600px;
 padding: 20px;

 .datatableTitle{
    width: 100%;
    font-size: 24px;
    color: gray;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .link{
        text-decoration: none;
        color: green;
        font-size: 16px;
        font-weight: 400;
        border: 1px solid green;
        padding: 5px;
        border-radius: 5px;
        background-color: rgba(5, 195, 5, 0.266);
        cursor: pointer;
    }
 }

 .cellWithImg{
    display: flex;
    align-items: center;

    .cellImg{
        width: 32px;
        height: 32px;
        border-radius: 50%;
        object-fit: cover;
        margin-right: 20px;
    }
 }
 .cellWithStatus{
    &.active{
        color: green;
        background-color: rgba(0, 128, 0, 0.104);
    }
    &.pending{
        color: orange;
        background-color: rgba(255, 166, 0, 0.212);
    }
    &.passive{
        color: goldenrod;
        background-color: rgba(218, 165, 32, 0.27);
    }

 }
 .cellAction{
    display: flex;
    align-items: center;
    gap: 15px;

    .viewButton{
        padding: 2px 5px;
        color: blue;
        background-color: rgba(0, 0, 255, 0.333);
        border-radius: 2px;
        border: 1px solid black;
        cursor: pointer;
    }
    .deleteButton{
        padding: 2px 5px;
        color: darkred;
        background-color: rgba(139, 0, 0, 0.274);
        border-radius: 2px;
        border: 1px solid black;
        cursor: pointer;
    }

 }
}

