.app.dark {
    background-color: #111;
    color: rgb(156, 156, 156);
  
    .chartGrid {
      stroke: rgba(228, 228, 228, 0.219);
    }
  
    .table {
      background-color: #111;
  
      .tableCell {
        color: gray;
      }
    }
  
    .navbar {
      color: #999;
      border-color: #333;
  
      .search {
        border-color: gray;
      }
    }
  
    .Sidebar {
      background-color: #111;
      border-color: #333;
  
      .top {
        .logo {
          color: #999;
        }
      }
      hr {
        border-color: #333;
      }
  
      ui {
        li {
          &:hover {
            background-color: #333;
          }
  
          .icon {
            color: #999;
          }
        }
      }
    }
  
    .Datatable {
      .datagrid {
        color: gray;
        border: none;
  
        .viewButton,
        .deleteButton,
        .cellWithStatus {
          color: gray;
          border: none;
          &.active{
            color: rgb(50, 255, 50);
            background-color: rgba(0, 128, 0, 0.454);
        }
        &.pending{
            color: orange;
            background-color: rgba(255, 166, 0, 0.212);
        }
        &.passive{
            color: goldenrod;
            background-color: rgba(218, 165, 32, 0.27);
        }
        }
      }
    }
  
    input{
        background-color: transparent;
    }
  }