.table{
    .cellWrapper{
        display:flex;
        align-items: center;
        .image{
            width: 32px;
            height: 32px;
            border-radius: 50%;
            margin: 10px;
            object-fit: cover;
        }
     }

     .status{
        padding: 5px;
        border-radius: 5px;

        &.Approved{
            color: green;
            background-color: rgba(0, 128, 0, 0.104);
        }
        &.Pending{
            color: orange;
            background-color: rgba(255, 166, 0, 0.212);
        }
     }
}